.dna-img {
  position: fixed;
  width: 50%;
  height: 50%;
}

.dna-img.t {
  top: 0;
  left: 0;
}

.dna-img.c {
  top: 0;
  right: 0;
}

.dna-img.a {
  bottom: 0;
  left: 0;
}

.dna-img.g {
  bottom: 0;
  right: 0;
}

section#top {
  position: relative;
  width: 100%;
  height: 750px;
  overflow: hidden;
  text-align: center;
}



